<template>
  <div>
    <div>
      <form>
      	<input type="text"></input>
      	<input type="text"></input>
      	<input type="button" value="登录">
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data(){
  	return{
  	loginUrl:"Login",
    loginModel:{
    	id:"",
        password:""
    },
    msg:""
    }
  },
  methods:{
  	login:function(){
  		var vm=this;
        vm.msg="";
        if(!this.isloginEmpty()){
        	vm.$http.post(vm.loginUrl,{              
                body:vm.loginModel,
                headers:{'Content-Type':'application/json;charset=utf-8'}
            }).then(function(res){
                console.log("登录成功");
                vm.msg="登录成功";                        
            })
        }
  	}
  }

       
}
</script>

<style>

</style>


